import React from 'react'
import { Bars } from '@styled-icons/fa-solid';
import styled  from 'styled-components'
import IconStyle from '../styled/components/icon'

const BarsIcon = styled(Bars)`
    ${IconStyle};
`

export default ({toogleDrawer, openDrawer}) => {
    return (<BarsIcon onClick={toogleDrawer} $lg={true}  className={["drawerBtn", openDrawer ? "fas fa-times":"fas fa-bars ml-3", "ml-4"]}/>)
}